<template>
    <MainLayout>
        <div class="static-page page-wrap">
            <div class="container">
                <Spinner v-if="isLoading"></Spinner>
                <div class="row">
                    <div class="col-md-12">
                        <h2>প্রতিলিপি সংকলন আনসাবস্ক্রাইব করুন</h2>
                        <div class="page-content">
                        </div>
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-6">
                        <div class="form-check">
                           <input v-model="unSubscribeOption" id="radio-option-1" class="form-check-input" type="radio" name="newsletter-option" value="TOO_MANY_EMAILS">
                           <label class="form-check-label" for="radio-option-1">আমি একাধিক ই-মেল পাচ্ছি</label>
                       </div>
                       <div class="form-check">
                           <input v-model="unSubscribeOption" id="radio-option-2" class="form-check-input" type="radio" name="newsletter-option" value="CONTENT_IRRELEVANT">
                           <label class="form-check-label" for="radio-option-2">বিষয়বস্তু আমার জন্য প্রাসঙ্গিক নয়</label>
                       </div>
                       <div class="form-check">
                           <input v-model="unSubscribeOption" id="radio-option-3" class="form-check-input" type="radio" name="newsletter-option" value="NO_TIME_TO_READ">
                           <label class="form-check-label" for="radio-option-3">আমার পড়ার সময়ের অভাব</label>
                       </div>
                       <div>
                            <button type="button" @click="unSubscribeUser" :disabled="!unSubscribeOption"  class="btn btn-save">আনসাবস্ক্রাইব</button>
                       </div>
                    </div>
                    <div class="col-md-6">
                        <img style="width: 100%" src="data:image/jpeg;base64,">
                    </div>
                </div>
            </div>
        </div>
    </MainLayout>
</template>

<script>
import MainLayout from '@/layout/main-layout.vue';
import constants from '@/constants';
import Spinner from '@/components/Spinner.vue';
import { mapGetters, mapActions } from 'vuex'

export default {
    components: {
        MainLayout,
        Spinner
    },
    data() {
        return {
            unSubscribeOption: null,
            isLoading: false
        }
    },
    computed: {
        
    },
    methods: {

        unSubscribeUser() {
            const that = this;
            this.isLoading=true;
            const {userId} = this.$route.query;
            if(!userId) {
                that.isLoading=false;
                alert("Invalid URL");
                return;
            }
            import('firebase').then(async (firebase) => {
              if (firebase.apps.length === 0) {
                const config = {
                                apiKey: process.env.FIREBASE_API_KEY,
                                authDomain: process.env.FIREBASE_AUTH_DOMAIN,
                                databaseURL: process.env.FIREBASE_DATABASE_URL,
                                storageBucket: process.env.FIREBASE_STORAGE_BUCKET
                            };
               firebase.initializeApp(config);
              }
               
               let node = firebase.database().ref( "PREFERENCE" ).child( userId );
               await node.update({
                   newsletterUnsubscribeReason: that.unSubscribeOption,
                   newsletterFrequency: "NEVER",
                   lastUpdated: firebase.database.ServerValue.TIMESTAMP
               })
               .then(()=> {
                  that.isLoading=false;
                  alert("You have been successfully unsubscribed.");
                  that.$router.push('/');
               })
               .catch(()=> {
                  that.isLoading=false;
                  alert("There was some error. Please try again")
               });
           })
           .catch(() => {
              that.isLoading=false;
              alert("There was some error. Please try again")
           });
        }
        
    },
    watch: {
        
    },
    created() {
        
    }
}
</script>

<style lang="scss" scoped>
.static-page {
    margin-top: 85px;
    text-align: left;
    min-height: 600px;
    @media screen and (max-width: 992px ) {
        margin-top: 65px;
    }
    h2 {
        font-size: 22px;
        font-weight: bold;
        text-align: left;
        border-left: 3px solid #d0021b;
        padding-left: 10px;
        margin: 10px 0;
        @media screen and (max-width: 992px ) {
            font-size: 18px;
        }
    }
    button {
       font-size: 14px;
       margin: 5px 4px 10px 0;
       float: left;
       &.btn-save {
           background: #d0021b;
           color: #fff;
       }
   }
   .form-check {
        padding: 5px 0px;
        label{
            padding-left: 5px;
        }
   }
}
</style>
<style>
p {
    word-break: break-word !important;
}
</style>



